@import "variables";

.quetionForm {
  margin: 0 auto;
  text-align: center;
  height: 500px;
  display: flex;
  align-items: center;
}
.quetionForm_Img {
  position: relative;
}
.quetionForm_Img img {
  width: 450px;
  height: 450px;
  position: relative;
  z-index: 1;
}
.quetionForm_Img_frame {
  position: absolute;
  width: 425px;
  height: 425px;
  border: 4px $gold solid;
  top: -5px;
  left: 20px;
  transition: 1s ease;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input:placeholder-shown + label {
  opacity: 0;
  transform: translateY(1rem);
}
.quetionForm_form {
  position: relative;
  z-index: 1;
  width: 400px;
  height: 300px;
  padding: 50px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.quetionForm_title {
  font-size: 45px;
  padding-bottom: 20px;
}
.formInput {
  font-family: Montserrat;
  font-size: 16px;
  font-style: italic;
  letter-spacing: 1px;
  width: 350px;
  border: 0;
  background-color: white;
  border-bottom: 1px $lightGray solid;
  //resize: none;
  overflow: auto;
  box-shadow: 0 0 0 30px white inset !important;
}
